<script>
export default {
  name: 'App',
  data () {
    return {
      menu: '',
      title: '',
      num: '',
      icon: ''
    }
  },
  destory () {
    this.$off('changeData')
  },
  created () {
    const _self = this

    this.$on('changeData', function (newMenu, newTitle, newNum, newIcon) {
      _self.menu = newMenu
      _self.title = newTitle
      _self.num = newNum
      _self.icon = newIcon
    })
  }
}
</script>

